function copyEmailAddr() {
  let copyText = document.getElementById("emailAddr");
  console.log(copyText);
  navigator.clipboard.writeText(copyText.innerHTML);
  alert("Copied:   " + copyText.innerHTML);
} 

export function contactContent(){
    return(
        <div id="contactStuff">
            <p>It would be great to work together! You can email me to: </p>
            <br/>
            <p id="emailAddr" onClick={copyEmailAddr}>niru.webdev@gmail.com</p>
            <br/>
            <p>You can also check my profiles on these networks:</p>
            <div id="contactIcons">
                <a href="https://www.freecodecamp.org/nico687" target="_blank" rel="noreferrer"><i class="fab fa-free-code-camp"></i></a>
                <a href="https://github.com/Niru-Ninja" target="_blank" rel="noreferrer"><i class="fa fa-github"></i></a>
                <a href="https://www.linkedin.com/in/nicol%C3%A1s-rueda-9373a01ba/" target="_blank" rel="noreferrer"><i class="fa fa-linkedin" aria-hidden="true"></i></a>
            </div>
        </div>
    );
};