import React from "react";
import ReactDOM from "react-dom";

import {aboutMeContent} from "./aboutMe.js";
import {contactContent} from "./contactMe.js";

function Icons(){
  return (
  <React.Fragment>
    <Icon window="aboutMe" imageSrc="./images/Icon-Note.png" altxt="Notepad icon" text="About me"/>
    <Icon window="myWork" imageSrc="./images/Icon-World.png" altxt="Planet earth icon" text="My work so far"/>
    <Icon window="contact" imageSrc="./images/Icon-Phone.png" altxt="Phone icon" text="Contact me"/>
  </React.Fragment>
  );
}

function Windows(){
  return(
    <React.Fragment>
      <Window id="aboutMe" title="About me" width="33vw" left="5%" top="5%" content={aboutMeContent()}/>
      <Window id="myWork" title="My work so far" width="50vw" left="30%" top="30%" content={<p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Sint, voluptate expedita! Atque excepturi veritatis velit repellendus quibusdam in laborum consequuntur.</p>}/>
      <Window id="contact" title="Contact me" width="25vw" left="60%" top="65%" content={contactContent()}/>
    </React.Fragment>
  );
}

function iconClick(event){
  let content0 = document.getElementById(event.currentTarget.childNodes[0].innerHTML);
  let content1 = content0.getElementsByClassName("windowContent")[0];
  let content2 = content0.getElementsByClassName("windowHeader")[0];
  content1.style.display = "block";
  content0.style.border = "2px solid #1e5060";
  content2.style.border = "0px";
  content2.style.borderBottom = "2px solid #1e5060";
  content0.style.display = "block";
}

const Icon = (props) => {
  return (
    <div className="icon" onClick={iconClick}>
      <var className="hidden" translate="no">{props.window}</var>
      <img src={props.imageSrc} alt={props.altxt}/>
      <p>{props.text}</p>
    </div>
  );
};

function minClick(event) {
  let content0 = event.target.parentNode.parentNode;
  let content1 = content0.getElementsByClassName("windowContent")[0];
  let content2 = content0.getElementsByClassName("windowHeader")[0];
  if (content1!==undefined && content1.style.display === "none") {
    content0.style.border = "2px solid #1e5060";
    content2.style.border = "0px";
    content2.style.borderBottom = "2px solid #1e5060";
    content1.style.display = "block";
  } 
  else if(content1!==undefined){
    content0.style.border = "0px";
    content2.style.borderBottom = "0px";
    content2.style.border = "2px solid #1e5060";
    content1.style.display = "none";
  }
}

function closeClick(event) {
  let content = event.target.parentNode.parentNode;
  if (content!==event.target && content!==undefined && content.style.display === "none") {
   content.style.display = "block";
  } 
  else if(content!==undefined){
   content.style.display = "none";
  }
}

const Window = (props) => {
  return (
    <div style={{left: props.left, top: props.top, width: props.width}} id={props.id} className="window draggable">
      <div style={{width: props.width}} className="windowHeader">
        <h3>{props.title}</h3>
        <button type="button" className="windowButton" onClick={minClick} translate="no">-</button>
        <button type="button" className="windowButton" onClick={closeClick} translate="no">x</button>
      </div>
      <div className="windowContent">
        {props.content}
      </div>
    </div>
  );
}

ReactDOM.render(<Icons/>, document.getElementById("iconContainer"));
ReactDOM.render(<Windows/>, document.getElementById("windowContainer"));